import React from 'react';
import './Home.css';
import  {Link} from 'react-router-dom';
import AppsIcon from '@material-ui/icons/Apps';
import {Avatar} from  '@material-ui/core';
import Search from './Search';


// AIzaSyBtCrbRngBn5nD_tw7_tLFZ3fxuZOHU3DE
// e07ebc041b994930b
function Home() {
    return (
        <div className="home">
            {/* <h1>Hellp</h1> */}
            <div className="home__header">
                <div className="home__headerLeft">
                    <Link to="/about">About</Link>
                    <Link to="/store">Store</Link>
                </div>
                <div className="home__headerRight">
                    <Link to="/about">Gmail</Link>
                    <Link to="/store">Images</Link>
                   <Link to=""> <AppsIcon/></Link>
                   <Link to=""> <Avatar/></Link>
               </div>    
            </div>


            <div className="home__body">
                <img src="https://www.google.com/logos/doodles/2015/googles-new-logo-5078286822539264.3-hp2x.gif" height={300} width={300} alt="googleimage" />
                <div className="input__container">
                    <Search/>
                </div>
            </div>

        </div>
    );
}

export default Home;